.ticket-card {
  display: block;
  background-color: $color-default-white;
  border: 7px solid $color-default-white;
  padding: 58px 20px;
  min-width: 360px;
  max-width: 360px;
  color: inherit;

  @include hover-focus {
    border-image: linear-gradient($color-tabs-red, $color-lightest-blue) 30;

    & .ticket-card__link {
      color: $color-default-white;
      background: linear-gradient(90deg, $color-red-gradient, $color-blue-gradient);
      outline: none;
      box-shadow: none;
    }
  }

  @include vp-1365 {
    max-width: 1266px;
  }

  @include vp-767 {
    min-width: 290px;
    padding: 43px 35px;
  }
}

.ticket-card:focus-within {
  border-image: linear-gradient($color-tabs-red, $color-lightest-blue) 30;
}

.ticket-card__title {
  text-align: center;
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;
  margin: 0 0 11px 0;

  @include vp-1365 {
    font-size: 24px;
    line-height: 24px;
    margin: 0 0 18px 0;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 22px;
  }
}

.ticket-card__description {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  text-align: center;
  margin: 0 0 12px 0;
  display: block;

  @include vp-1365 {
    margin: 0 0 18px 0;
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 22px;
  }
}

.ticket-card__wrapper {
  position: relative;
  margin-bottom: 30px;

  @include vp-1365 {
    margin: 0 0 18px 0;
  }
}

.ticket-card__shadow {
  color: $color-background-grey;
  font-size: 100px;
  font-style: normal;
  font-weight: 700;
  line-height: 150%;
  display: block;
  text-align: center;

  @include vp-767 {
    font-size: 80px;
  }
}

.ticket-card__price {
  color: $color-lightest-blue;
  text-align: center;
  position: absolute;
  bottom: 5px;
  left: 0;
  right: 0;
  font-size: 60px;
  font-weight: 700;
  line-height: 150%;

  @include vp-767 {
    font-size: 48px;
  }
}

.ticket-card__rouble {
  font-family: $ff-roboto;
  color: $color-base-blue;
}

.ticket-card__link {
  width: 200px;
  margin: 0 auto;
}
