.jury {
  margin: 0;
  padding: 0;
  list-style: none;
  color: $color-default-white;
}

.jury__name {
  font-size: 22px;
  font-weight: 400;
  line-height: 22px;
  text-align: center;
  margin-bottom: 8px;
  display: block;

  &--photo {
    position: absolute;
    right: 0;
    left: 0;
    bottom: 18px;
    text-align: center;
  }
}

.jury__item {
  position: relative;

  @include hover {
    & .jury__content {
      opacity: 1;
      transition: opacity $trans-default;
    }

    & .jury__name--photo {
      display: none;
    }
  }

  @include vp-767 {
    height: 274px;
  }
}

.jury__item img {
  width: 100%;
  height: 100%;
}

.jury__content {
  opacity: 0;
  z-index: 2;
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  bottom: 0;
  padding: 45px 40px 50px 40px;
  background-color: rgba($color-base-blue, 0.89);

  @include vp-767 {
    padding: 35px 30px;
  }
}

.jury__profession {
  display: block;
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  color: #b6bdcc;
  text-align: center;
  margin-bottom: 33px;
}

.jury__list {
  margin: 0;
  padding: 0 0 0 20px;
  list-style: outside;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
}
