.feedback h2 {
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin: 0 0 46px 0;
  text-align: center;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
    margin: 0 0 42px 0;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
    margin: 0 0 46px 0;
  }
}

.feedback__list {
  box-shadow: 0 0 40px 0 rgba($color-light-blue, 0.2);
}

.feedback article {
  width: 560px;
  background-color: $color-light-blue;
  padding: 60px;
  color: $color-default-white;
  display: flex;
  gap: 30px;
  margin: 0 auto;

  @include vp-1365 {
    width: 580px;
    padding: 60px 65px;
  }

  @include vp-767 {
    width: 226px;
    padding: 30px 25px;
    flex-direction: column;
    gap: 10px;
  }
}

.feedback img {
  width: 80px;
  height: 80px;
  margin: 0 auto;

  @include vp-767 {
    width: 68px;
    height: 68px;
  }
}

.feedback h3 {
  font-size: 22px;
  font-weight: 400;
  line-height: 22px;
  margin: 0 0 21px 0;

  @include vp-1365 {
    font-size: 20px;
    line-height: 20px;
    margin: 0 0 13px 0;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 20px;
    margin: 0 0 16px 0;
    text-align: center;
  }
}

.feedback p {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  margin: 0;

  @include vp-767 {
    font-size: 14px;
    line-height: 22px;
    text-align: center;
  }
}
