.intro {
  background: $color-background-gradient;
}

.intro__container {
  display: flex;
  align-items: center;
  gap: 70px;
  padding-top: 86px;

  @include vp-1365 {
    gap: 30px;
  }

  @include vp-767 {
    padding-top: 89px;
  }
}

.intro__wrapper {
  width: 40%;
  max-width: 385px;
  display: grid;
  grid-template-columns: 115px 270px;
  grid-template-areas:
    "list icon"
    "list title"
    "list link";

  @include vp-1365 {
    width: 50%;
    gap: 0 25px;
    grid-template-columns: 115px 185px;
  }

  @include vp-767 {
    max-width: fit-content;
    width: 100%;
    gap: 0 15px;
    grid-template-areas:
      "list icon"
      "list title"
      "link link";
    grid-template-columns: auto auto;
    margin: 0 auto;
  }
}

.intro__list {
  padding: 0;
  list-style: none;
  min-width: 113px;
  grid-area: list;
  margin: auto 0;

  @include vp-767 {
    margin: auto 0 60px 0;
  }
}

.intro__item {
  display: block;
  text-align: end;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: $color-default-white;

  @include vp-767 {
    line-height: 20px;
  }
}

.intro__icon {
  display: block;
  color: $color-default-white;
  text-align: end;
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin-bottom: 10px;
  grid-area: icon;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    margin-bottom: 14px;
  }
}

.intro__title {
  color: $color-default-white;
  text-align: end;
  font-family: $ff-alumni-sans;
  font-size: 92px;
  font-weight: 800;
  line-height: 110px;
  letter-spacing: 4.6px;
  text-transform: uppercase;
  padding: 0;
  margin: 0 0 50px 0;
  grid-area: title;

  @include vp-1365 {
    font-size: 72px;
    line-height: 85px;
    letter-spacing: normal;
  }

  @include vp-767 {
    font-size: 62px;
    line-height: 70px;
    letter-spacing: 1.86px;
    margin: 0 0 60px 0;
  }
}

.intro__link {
  display: block;
  width: 230px;
  margin-left: auto;
  grid-area: link;

  @include vp-1365 {
    width: 185px;
  }

  @include vp-767 {
    width: 100%;
  }
}

.intro__img-wrapper {
  width: 53%;
  max-width: 500px;

  @include vp-1365 {
    width: 50%;
  }

  @include vp-767 {
    display: none;
  }
}


.intro__img-wrapper img {
  @include vp-1365 {
    height: 390px;
    width: 324px;
  }

  @include vp-767 {
    display: none;
  }
}
