.footer {
  background-color: $color-background-black;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;

  @include vp-1365 {
    padding-top: 45px;
    padding-bottom: 45px;
  }

  @include vp-767 {
    padding-top: 30px;
    flex-direction: column;
    gap: 0;
  }
}

.footer__logo-link {
  flex-shrink: 0;

  @include hover-focus {
    opacity: 0.7;
  }

  @include active {
    opacity: 0.3;
  }

  @include vp-767 {
    margin: 0 auto 30px 0;
    display: block;
  }
}

.footer__logo-link img {
  @include vp-1365 {
    width: 100px;
  }

  @include vp-767 {
    width: 85px;
  }
}

.footer__list {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;

  @include vp-1365 {
    gap: 10px;
  }

  @include vp-767 {
    gap: 10px 15px;
    margin-bottom: 55px;
  }
}

.footer__social-list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.footer__link {
  padding: 4px 9px;
  border: 1px solid $color-transparent;
  color: $color-default-white;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  position: relative;

  @include hover {
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 10px;
      right: 10px;
      height: 1px;
      background-color: $color-default-white;
    }
  }

  @include focus {
    border-color: $color-default-white;
    outline: none;
  }

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}

.footer__social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: $color-default-white;
  color: $color-default-black;

  @include hover-focus {
    outline: none;
    background-color: $color-lightest-blue;
    color: $color-default-white;
  }

  @include active {
    background-color: $color-base-blue;
  }

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}
