.contacts {
  display: grid;
  grid-template-columns: 1fr 1fr;

  @include vp-1365 {
    display: block;
  }
}

.contacts form {
  background-color: $color-base-blue;
}

.contacts__form {
  width: 683px;
  margin: 0 0 0 auto;
  padding: 60px 170px 60px 100px;

  @include vp-1365 {
    margin: 0 auto;
    padding: 100px 133px;
    width: 100%;
  }

  @include vp-767 {
    padding: 70px 15px;
  }
}

.contacts__title {
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin: 0 0 40px 0;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
    margin: 0 0 36px 0;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
    margin: 0 0 40px 0;
  }

  &--white-mod {
    color: $color-default-white;
    margin: 0 0 29px 0;

    @include vp-1365 {
      margin: 0 0 22px 0;
    }
  }
}

.contacts__wrapper {
  width: 683px;
  margin: 0 auto 0 0;
  padding: 60px 120px 60px 120px;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 200vw;
    height: 1px;
    background-color: $color-base-blue;
  }

  @include vp-1365 {
    width: 100%;
    margin: 0 auto;
    padding: 105px 133px 100px 133px;
  }

  @include vp-767 {
    padding: 70px 15px;
  }
}

.contacts__list {
  display: grid;
  grid-auto-flow: column;
  grid-template-columns: 1.4fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 15px 80px;
  margin: 0;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    bottom: 0;
    width: 1px;
    background-color: #74819c;
  }

  @include vp-1365 {
    gap: 27px 130px;
    grid-template-columns: 1fr 1fr;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 28px;

    &::after {
      content: "";
      display: none;
    }
  }
}

.contacts__term {
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  margin-bottom: 5px;

  @include vp-1365 {
    font-size: 22px;
    line-height: 22px;
    margin-bottom: 10px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
  }
}

.contacts__description {
  margin: 0;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;

  @include vp-767 {
    line-height: 20px;
  }
}

.contacts__button {
  width: 100%;
}
