.header {
  padding-top: 30px;
  padding-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $color-transparent;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;

  @include vp-1365 {
    padding-top: 30px;
    padding-bottom: 30px;
  }

  @include vp-767 {
    padding-top: 16px;
    padding-bottom: 16px;
  }
}

.header__logo {
  flex-shrink: 0;

  @include hover-focus {
    opacity: 0.7;
  }

  @include active {
    opacity: 0.3;
  }
}

.header__logo img {
  @include vp-1365 {
    width: 111px;
  }

  @include vp-767 {
    width: 102px;
  }
}

.header__phone {
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;
  color: $color-default-white;
  position: relative;

  @include vp-1365 {
    font-size: 24px;
    line-height: 24px;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 22px;
  }

  @include hover-focus {
    outline: none;
    color: $color-light-blue;
  }

  @include active {
    color: $color-base-blue;

    &::after {
      content: "";
      position: absolute;
      bottom: -4px;
      left: 0;
      right: 0;
      height: 1px;
      background-color: $color-base-blue;
    }
  }

  &:disabled {
    color: $color-background-grey;
    opacity: 0.5;
  }
}
