.announcement {
  display: grid;
  grid-template-columns: 5fr 6.6fr;

  @include vp-1365 {
    grid-template-columns: 292fr 386fr;
  }

  @include vp-767 {
    display: block;
  }
}

.announcement__content {
  padding: 80px;
  background-color: $color-background-red;
  color: $color-default-white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  @include vp-1365 {
    padding: 40px 45px;
  }

  @include vp-767 {
    padding: 40px 40px;
  }
}

.announcement__wrapper {
  display: flex;
  gap: 68px;
  margin-bottom: 23px;

  @include vp-1365 {
    margin-bottom: 6px;
    gap: 40px;
  }

  @include vp-767 {
    margin-bottom: 13px;
    gap: 50px;
  }
}

.announcement__title {
  font-family: $ff-alumni-sans;
  font-size: 82px;
  font-weight: 600;
  line-height: 76px;
  letter-spacing: 2.46px;
  text-transform: uppercase;
  margin: 0 0 auto 0;

  @include vp-1365 {
    font-size: 48px;
    line-height: 46px;
    letter-spacing: 1.44px;
  }
}

.announcement__list {
  margin: 0;
}

.announcement__term {
  font-size: 18px;
  font-weight: 300;
  line-height: 24px;
  padding-bottom: 8px;

  @include vp-1365 {
    padding: 0;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 20px;
  }
}

.announcement__term ~ .announcement__term {
  margin-top: 20px;

  @include vp-1365 {
    margin-top: 8px;
  }
}

.announcement__description {
  font-size: 40px;
  font-weight: 700;
  line-height: 40px;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 6px;
  width: fit-content;
  padding-top: 4px;
  border-top: 1px solid $color-default-white;

  @include vp-1365 {
    font-size: 24px;
    line-height: 24px;
    padding-top: 2px;
    gap: 3px;
  }
}

.announcement__data {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;

  @include vp-1365 {
    font-size: 10px;
    line-height: 10px;
  }
}

.announcement__text {
  margin: 0 0 55px 0;
  font-size: 18px;
  line-height: 24px;

  @include vp-1365 {
    margin: 0 0 20px 0;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 20px;
  }
}
