.promotion h2 {
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin: 0 0 70px 0;
  text-align: center;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
    margin: 0 0 40px 0;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
  }
}

.promotion__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;

  @include vp-1365 {
    display: flex;
    flex-direction: column;
    gap: 30px;
  }

  @include vp-767 {
    gap: 33px;
  }
}

.promotion__main {
  display: grid;
  grid-template-columns: 1fr 1fr;
  min-height: 460px;

  @include vp-1365 {
    grid-template-columns: 2.8fr 4fr;
  }

  @include vp-767 {
    display: block;
  }
}

.promotion__main article {
  display: flex;
  flex-direction: column;
  padding: 40px 45px;
  background-color: $color-background-red;
  color: $color-default-white;

  @include vp-1365 {
    grid-template-columns: 2.8fr 4fr;
  }

  @include vp-767 {
    display: block;
  }
}

.promotion__main h3 {
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;
  margin: 0 0 15px 0;

  @include vp-1365 {
    font-size: 24px;
    line-height: 24px;
    margin: 0 0 5px 0;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 22px;
    margin: 0 0 9px 0;
    text-align: center;
  }
}

.promotion__main span {
  display: block;
  font-size: 70px;
  font-weight: 700;
  line-height: 70px;
  margin: 0 0 25px 0;

  @include vp-1365 {
    margin: 0 0 20px 0;
  }

  @include vp-767 {
    font-size: 64px;
    line-height: 64px;
    text-align: center;
  }
}

.promotion__main p {
  font-size: 18px;
  font-weight: 300;
  line-height: 24px;
  margin: 0 0 auto 0;

  @include vp-767 {
    font-size: 16px;
    line-height: 20px;
    margin: 0 0 31px 0;
    text-align: center;
  }
}

.promotion a {
  display: block;
  color: $color-default-white;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  padding: 10px;
  width: fit-content;
  position: relative;
  top: 5px;
  left: -10px;

  &::after {
    content: "";
    position: absolute;
    bottom: 6px;
    left: 10px;
    right: 10px;
    height: 1px;
    background-color: $color-default-white;
  }

  @include hover {
    opacity: 0.7;
  }

  @include focus {
    box-shadow: 0 0 0 2px $color-default-white;
    outline: none;

    &::after {
      display: none;
    }
  }

  @include active {
    opacity: 1;
  }

  @include vp-767 {
    margin: 0 auto;
    top: 0;
    left: 0;
  }
}

.promotion__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 40px;

  @include vp-1365 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
  }

  @include vp-767 {
    display: flex;
  }
}

.promotion__wrapper article {
  padding: 23px 28px 28px 28px;
  border-image: linear-gradient($color-tabs-red, $color-lightest-blue) 30;
  border-width: 7px;
  border-style: solid;
  display: flex;
  flex-direction: column;
  min-height: 210px;

  @include vp-767 {
    padding: 38px 40px 28px 40px;
  }
}

.promotion__wrapper h3 {
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;
  margin: 0 0 10px 0;

  @include vp-1365 {
    font-size: 24px;
    line-height: 24px;
  }

  @include vp-767 {
    font-size: 22px;
    line-height: 22px;
    text-align: center;
  }
}

.promotion__wrapper p {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  margin: 0 0 auto 0;

  @include vp-767 {
    font-size: 14px;
    line-height: 22px;
    text-align: center;
    margin: 0 0 21px 0;
  }
}

.promotion__wrapper a {
  color: $color-base-blue;

  &::after {
    background-color: $color-base-blue;
  }

  @include focus {
    box-shadow: 0 0 0 2px $color-base-blue;
  }
}

.promotion__img-wrapper img {
  height: 100%;
  width: 100%;
}
