.advantages {
  background-color: $color-background-grey;
}

.advantages__container {
  @include vp-767 {
    padding-left: 0;
    padding-right: 0;
  }
}

.advantages article {
  display: flex;
  gap: 30px;
  margin-bottom: 15px;

  @include vp-1365 {
    width: 315px;
    gap: 14px;
    margin-bottom: 0;
  }

  @include vp-767 {
    width: 100%;
    display: block;
    padding: 0 55px;
  }
}

.advantages__wrapper {
  width: fit-content;
  margin: 0 auto;
  position: relative;
  display: grid;
  grid-template-columns: 340px 340px;
  gap: 123px 360px;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 340px;
    height: 2px;
    background-color: rgba($color-tabs-red, 0.2);
  }

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 340px;
    height: 2px;
    background-color: rgba($color-tabs-red, 0.2);
  }

  @include vp-1365 {
    display: flex;
    flex-wrap: wrap;
    gap: 40px 25px;
    justify-content: space-between;

    &::after {
      content: "";
      display: none;
    }

    &::before {
      content: "";
      display: none;
    }
  }

  @include vp-767 {
    width: 100%;
    flex-wrap: nowrap;
    justify-content: start;
    flex-direction: column;
    gap: 35px;
  }
}

.advantages h2 {
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin: 0 0 50px 0;
  text-align: center;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
    margin-bottom: 55px;
  }
}

.advantages span {
  display: block;
  font-size: 70px;
  font-weight: 700;
  line-height: 70px;
  background: linear-gradient(128deg, $color-red-gradient, $color-blue-gradient);
  background-clip: text;
  -webkit-text-fill-color: transparent;

  @include vp-767 {
    font-size: 64px;
    line-height: 64px;
    width: fit-content;
    margin: 0 auto;
  }
}

.advantages b {
  display: block;
  font-size: 14px;
  font-weight: 400;
  line-height: 22px;
  color: $color-neon-blue;

  @include vp-767 {
    line-height: 20px;
    text-align: center;
    margin-bottom: 12px;
  }
}

.advantages h3 {
  font-size: 22px;
  font-weight: 400;
  line-height: 22px;
  margin: 0 0 8px 0;

  @include vp-1365 {
    font-size: 20px;
    line-height: 20px;
    margin: 0 0 12px 0;
  }

  @include vp-767 {
    font-size: 18px;
    text-align: center;
  }
}

.advantages p {
  font-size: 16px;
  line-height: 24px;
  margin: 0;

  @include vp-767 {
    text-align: center;
    font-size: 14px;
    line-height: 22px;
  }
}

.advantages__main {
  position: absolute;
  top: 0;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);

  @include vp-1365 {
    position: static;
    width: 100%;
    transform: none;
  }

  @include vp-767 {
    display: block;
    margin: 5px 0;
  }
}

.advantages__main article {
  display: block;
  width: 240px;
  height: 100%;
  background-color: $color-light-blue;
  padding: 70px 34px;
  color: $color-default-white;
  margin-bottom: 0;

  @include vp-1365 {
    width: 400px;
    padding: 35px 70px;
    margin: 0 auto;
  }

  @include vp-767 {
    width: 100%;
    padding: 30px 30px 40px 30px;
  }
}

.advantages__main span {
  display: block;
  font-size: 70px;
  font-weight: 700;
  line-height: 70px;
  text-align: center;
  margin-bottom: 12px;
  -webkit-text-fill-color: $color-default-white;

  @include vp-1365 {
    margin-bottom: 25px;
  }

  @include vp-767 {
    font-size: 64px;
    line-height: 64px;
    margin-bottom: 5px;
  }
}

.advantages__main h3 {
  font-size: 26px;
  font-weight: 700;
  line-height: 26px;
  margin-bottom: 42px;
  text-align: center;

  @include vp-1365 {
    font-size: 22px;
    line-height: 22px;
    margin-bottom: 25px;
  }

  @include vp-767 {
    margin-bottom: 20px;
  }
}

.advantages__main p {
  text-align: center;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;

  @include vp-767 {
    font-size: 14px;
    line-height: 22px;
  }
}
