.slider {
  position: relative;
  height: 100%;
  margin: 0 auto;

  @include vp-1365 {
    width: 552px;
  }

  @include vp-767 {
    width: 226px;
  }

  &--feedback-mod {
    width: 560px;

    @include vp-1365 {
      width: 580px;
    }

    @include vp-767 {
      width: 230px;
    }
  }
}

.slider__button {
  position: absolute;
  top: 50%;
  width: 56px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 1px solid $color-transparent;
  color: $color-base-blue;
  cursor: pointer;

  &--next {
    right: -74px;
    transform: translateY(-50%);
  }

  &--prev {
    left: -74px;
    transform: translateY(-50%) rotateY(180deg);
  }

  @include vp-767 {
    width: 40px;
    height: 40px;

    &--next {
      right: -45px;
      transform: translateY(-50%);
    }

    &--prev {
      left: -45px;
      transform: translateY(-50%) rotateY(180deg);
    }
  }

  @include hover-focus {
    border: 1px solid $color-base-blue;
    outline: none;
  }

  @include active {
    color: $color-default-white;
    background-color: $color-base-blue;
  }
}

.slider__button svg {
  margin-left: 3px;

  @include vp-767 {
    width: 20px;
    height: 20px;
    margin-left: 2px;
  }
}

.slider__button--stop-mod.swiper-button-disabled {
  opacity: 0.5;
  pointer-events: none;
}

