.questions {
  background-color: $color-background-grey;
}

.questions h2 {
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin: 0 0 50px 0;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
    text-align: center;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
    margin: 0 0 37px 0;
  }
}

.questions__wrapper {
  display: grid;
  grid-template-columns: 26fr 86fr;
  gap: 40px;

  @include vp-1365 {
    display: flex;
    flex-direction: column;
  }

  @include vp-767 {
    gap: 30px;
  }
}

.questions__buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;

  @include vp-1365 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }

  @include vp-767 {
    grid-template-columns: 1fr 1fr;
    gap: 8px;
  }
}

.questions button {
  display: block;
  color: $color-base-blue;
  text-align: center;
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
  padding: 18px 20px;
  box-shadow: 0 0 0 2px rgba($color-base-blue, 0.2);
  background-color: $color-transparent;
  cursor: pointer;
  border: none;

  @include vp-767 {
    padding: 14px 20px;
    font-size: 16px;
    line-height: 16px;
  }

  &.is-active {
    color: $color-default-white;
    background: linear-gradient(
      90deg,
      $color-red-gradient,
      $color-blue-gradient
    );
    outline: none;
    box-shadow: none;
  }

  @include hover-focus {
    color: $color-default-white;
    background: linear-gradient(
      90deg,
      $color-red-gradient,
      $color-blue-gradient
    );
    outline: none;
    box-shadow: none;
  }

  @include active {
    color: rgba($color-base-blue, 0.3);
  }

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}

.questions__categories ul {
  position: absolute;
  visibility: hidden;
  padding: 0;
  margin: 0;
  list-style: none;
  border-bottom: 1px solid rgba($color-light-blue, 0.2);
  opacity: 0;
  transition: opacity $trans-600;

  &.is-open {
    position: static;
    visibility: visible;
    opacity: 1;
  }
}

.questions__categories details {
  padding: 25px 0;
  border-top: 1px solid rgba($color-light-blue, 0.2);

  @include hover-focus {
    & span {
      background-color: $color-light-blue;
    }

    & span::before {
      background-color: $color-default-white;
    }

    & span::after {
      background-color: $color-default-white;
    }
  }

  &[open] {
    & span {
      background-color: $color-light-blue;
    }

    & span::before {
      display: none;
    }

    & span::after {
      background-color: $color-default-white;
    }
  }

  @include vp-767 {
    padding: 21px 0;
  }
}

.questions__categories summary {
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 60px;
  cursor: pointer;

  @include vp-1365 {
    font-size: 22px;
    line-height: 22px;
  }

  @include vp-767 {
    font-size: 20px;
    line-height: 20px;
    gap: 22px;
  }
}

.questions__categories span {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: 1px solid $color-light-blue;
  position: relative;
  flex-shrink: 0;

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
    left: 50%;
    width: 50%;
    height: 5%;
    background-color: $color-light-blue;
  }

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
    left: 50%;
    width: 5%;
    height: 50%;
    background-color: $color-light-blue;
  }

  @include vp-767 {
    width: 30px;
    height: 30px;
  }
}

details[open] p {
  animation: slide $trans-600;
}

@keyframes slide {
  0% {
    opacity: 0;
    transform: translate(0, -20px);
  }

  100% {
    opacity: 1;
    transform: translate(0, 0);
  }
}

.questions__categories p {
  font-size: 18px;
  font-weight: 300;
  line-height: 24px;
  margin: 0 200px 0 0;

  @include vp-1365 {
    margin: 0 120px 0 0;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 20px;
    margin: 20px 0 0 0;
  }
}
