.gym {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 120px 100px 240px 100px;
  display: grid;
  grid-template-columns: 350px 660px;
  gap: 150px;
  position: relative;

  @include vp-1365 {
    padding: 100px 45px 183px 45px;
    display: block;
  }

  @include vp-767 {
    padding: 70px 0;
  }
}

.gym article {
  @include vp-1365 {
    margin: 0 0 36px 0;
  }

  @include vp-767 {
    margin: 0 15px 50px 15px;
  }
}

.gym h2 {
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin: 0 0 25px 0;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
    margin: 0 0 20px 0;
    text-align: center;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
    margin: 0 0 13px 0;
    text-align: start;
  }
}

.gym span {
  display: block;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  margin-bottom: 95px;

  @include vp-1365 {
    font-size: 20px;
    line-height: 20px;
    margin: 0 0 36px 0;
    text-align: center;
  }

  @include vp-767 {
    font-size: 18px;
    line-height: 20px;
    margin: 0 0 40px 0;
    text-align: start;
  }
}

.gym p {
  font-size: 16px;
  font-weight: 300;
  line-height: 24px;
  margin: 0;

  @include vp-1365 {
    text-align: center;
    width: 520px;
    margin: 0 auto;
  }

  @include vp-767 {
    width: fit-content;
    font-size: 14px;
    line-height: 22px;
    text-align: start;
  }
}

.gym p ~ p {
  margin-top: 10px;

  @include vp-1365 {
    display: none;
  }
}

.gym__video-wrapper {
  position: absolute;
  bottom: 120px;
  left: 53%;
  transform: translateX(-50%);
  width: 364px;

  @include vp-1365 {
    bottom: 100px;
    width: 35%;
    left: 50%;
  }

  @include vp-767 {
    position: relative;
    top: 0;
    left: 0;
    transform: none;
    width: 100%;
    height: 167px;
  }
}

.gym__img-wrapper {
  @include vp-767 {
    margin-bottom: 40px;
  }
}

.gym__img-wrapper img {
  @include vp-1365 {
    width: 100%;
    height: 100%;
  }
}

.gym__video-wrapper button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  border: 1px solid $color-background-red;
  background-color: $color-transparent;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: $color-default-white;
  cursor: pointer;

  @include hover-focus {
    color: $color-base-blue;
    border-color: $color-base-blue;
    outline: none;

    &::after {
      background: $color-default-white;
    }
  }

  @include active {
    &::after {
      width: 62px;
      height: 62px;
    }

    @include vp-1365 {
      &::after {
        width: 47px;
        height: 47px;
      }
    }
  }

  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    width: 72px;
    height: 72px;
    border-radius: 50%;
    background: linear-gradient(128deg, $color-red-gradient, $color-blue-gradient);
    z-index: -1;
  }

  @include vp-1365 {
    width: 72px;
    height: 72px;

    &::after {
      width: 52px;
      height: 52px;
    }
  }

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }
}

.gym__video-wrapper svg {
  margin-left: 3px;

  @include vp-1365 {
    width: 16px;
  }
}

.gym__video-wrapper video {
  width: 100%;
  height: 100%;
}
