.ticket {
  background-color: $color-transparent;
}

.ticket__container {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 100%;
    width: 300vw;
    background-color: $color-background-grey;
    z-index: -2;
  }

  @include vp-1365 {
    padding-left: 164px;
    padding-right: 164px;
  }

  @include vp-767 {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.ticket__title {
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  margin: 0 0 38px 0;
  text-align: center;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
    margin: 0 0 35px 0;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
    margin: 0 0 24px 0;
  }
}

.ticket__button-list {
  position: relative;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 50px;
  list-style: none;
  width: fit-content;
  margin: 0 auto 37px auto;

  @include vp-1365 {
    margin: 0 auto 45px auto;
    gap: 54px;
  }

  @include vp-767 {
    margin: 0 auto 35px auto;
    gap: 45px;
  }

  &::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    height: 2px;
    background-color: rgba($color-border-grey, 0.2);
    z-index: 5;
  }
}

.ticket__button-item {
  flex-shrink: 0;
}

.ticket__button {
  color: #102654;
  font-family: $ff-roboto;
  font-size: 20px;
  font-weight: 400;
  line-height: 20px;
  background-color: $color-transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  padding-bottom: 14px;
  position: relative;
  opacity: 0.5;

  &.is-active {
    opacity: 1;

    &::after {
      content: "";
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      height: 2px;
      background-color: $color-tabs-red;
    }
  }

  @include hover-focus {
    outline: none;
    opacity: 1;

    &::after {
      content: "";
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      height: 2px;
      background-color: $color-tabs-red;
      z-index: 10;
    }
  }

  @include active {
    opacity: 1;

    &::after {
      content: "";
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      height: 2px;
      background-color: $color-tabs-red;
      z-index: 10;
    }
  }

  @include vp-1365 {
    font-size: 18px;
    line-height: 18px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 16px;
  }

  &:disabled {
    opacity: 0.2;
    pointer-events: none;
  }
}

.ticket__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
  padding: 0;
  margin: 0;
  list-style: none;

  @include vp-1365 {
    display: flex;
    flex-direction: column;
    gap: 42px;
  }

  @include vp-767 {
    gap: 37px;
  }
}

.ticket__img-wrapper img {
  position: absolute;
  bottom: -152px;
  right: -185px;
  z-index: -1;
  height: auto;
  width: auto;

  @include vp-1365 {
    right: -80px;
    width: 400px;
  }

  @include vp-767 {
    display: none;
  }
}
